}body{
  padding:0;
  margin: 0;
  font-family: "Microsoft Yahei";
  font-size: 14px;
}
/*垂直居中*/
.vertical-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
}
p{
  margin: 0;
}
ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
a,a:hover,a:active{
  text-decoration: none;
}
